/* ------- Global styles ------- */
.projects-page {
   @media (min-width: 1400px) {
      max-width: 1400px;
      margin: 0 auto;
   }
}

/* ----- Card (global styles) ----- */
.projects-page .cards {
   padding-bottom: 40px;

   @media (min-width: 820px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
   }

   @media (min-width: 1100px) {
      margin-top: -25px;
   }
}

.projects-page .cards .card {
   padding: 15px 0;

   @media (min-width: 798px) {
      padding: 30px 0;
   }

   @media (min-width: 820px) {
      width: 540px;
   }

   &:first-child {
      margin-top: -15px;

      @media (min-width: 798px) {
         margin-top: -30px;
      }

      @media (min-width: 1100px) {
         margin-top: 0;
      }
   }
}

/* - Right content - */
.projects-page .card-content-right {
   @media (min-width: 1160px) {
      display: flex;
      flex-direction: column;
      min-height: 365px;
   }
}

.projects-page .card-intro {
   @media (min-width: 820px) {
      margin: 0 0 35px;
   }
}

.projects-page .view-more {
   @media (min-width: 1160px) {
      margin-top: auto;
   }
}
